import React, { useEffect } from 'react';
import { shortenAddress, useEthers } from '@usedapp/core';
import { CONFIG } from '../config';
import { BigNumber } from 'ethers';
import { Button } from './Button';

export const ConnectButton: React.FC = () => {
  const { account, activateBrowserWallet, library, chainId } = useEthers();

  useEffect(() => {
    if (!account) return;
    if (Number(chainId) === Number(CONFIG.CHAIN_ID)) return;
    if (chainId == null || library == null) return;

    void library.send('wallet_addEthereumChain', [
      {
        chainId: BigNumber.from(CONFIG.CHAIN_ID).toHexString(),
        chainName: CONFIG.CHAIN_NAME,
        rpcUrls: [CONFIG.RPC_URL],
        nativeCurrency: CONFIG.NATIVE_CURRENCY,
        blockExplorerUrls: [CONFIG.BLOCK_EXPLORER_URL],
      },
    ]);
  }, [account, chainId, library]);

  if (account) return <Button title={account}>{shortenAddress(account)}</Button>;

  return <Button onClick={activateBrowserWallet}>Connect wallet</Button>;
};
